.rules {
  margin: -100px 0 25px 0;

  &__box {
    background-color: $dark-blue;
    border: 5px solid $white;
    padding: 30px 35px;
    border-radius: 75px;
  }

  &__text {
    margin-bottom: 15px;
    line-height: 1.5;

    &--right {
      margin-bottom: 0;
      text-align: right;
      font-size: 15px;
    }
  }
}

.options {
  display: none;

  &--open {
    display: block;
    margin-top: -50px;
  }

  form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 15px -15px;
  }

  &__item {
    width: calc(20% - 30px);
    margin: 15px;
    transition: 200ms transform ease;

    &--active, &:hover, &:active, &:focus {
      transform: scale(1.15);
    }

    label {
      display: block;
      margin: 0 auto;
      text-align: center;
      cursor: pointer;
    }

    input[type="radio"] {
      display: none;
    }
  }

  &--disabled .options__item {
    label {
      cursor: auto;
    }

    &:not(.options__item--active) {
      &:hover, &:active, &:focus {
        transform: scale(1);
      }
    }
  }
}

.games-count {
  margin-bottom: 35px;

  &__box {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    background-color: $dark-blue;
    border: 5px solid $white;
    border-radius: 75px;
    padding: 15px;
    min-width: 300px;
    width: 30%;
    height: 150px;
    text-align: center;
    margin: 0 auto;

    &-option {
      border-radius: 50%;
      border: 3px solid $white;
      font-size: 35px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 55px;
      height: 55px;
      cursor: pointer;
      transition: 200ms background-color ease, 200ms transform ease;

      &--active, &:hover, &:active, &:focus {
        transform: scale(1.25);
      }

      &--active {
        background-color: $medium-blue;
      }
    }
  }

  &__title {
    margin-bottom: 5px;
  }

  form {
    width: 100%;
    max-width: 225px;
    display: flex;
    justify-content: space-around;

    input[type="radio"] {
      display: none;
    }
  }
}

.outcome {
  display: none;
  background-color: $medium-blue;
  border-top: 5px solid $white;
  padding: 50px 0;
  margin-top: 75px;

  &--open {
    display: block;
    position: relative;

    &:after {
      content: '';
      position: absolute;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
      width: 5px;
      background-color: $white;
      height: 100%;
    }
  }

  &__sides {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -145px;
    margin-bottom: 45px;

    &-item {
      width: calc(40% - 15px);
      display: flex;
      justify-content: center;
    }

    &-score {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      align-items: center;
      background-color: $dark-blue;
      border: 5px solid $white;
      border-radius: 50%;
      padding: 15px;
      width: 150px;
      height: 150px;
      text-align: center;
    }

    &-who {
      margin-bottom: 5px;
    }

    p {
      width: 100%;
      font-size: 100px;
      line-height: 75px;
    }
  }

  &__choices {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -15px;

    &-item {
      width: 20%;
      padding: 0 15px;
      position: relative;

      &--winner {
        width: 40%;
        z-index: 100;
      }
    }
  }

  &__result {
    display: none;
    font-size: 125px;
    text-align: center;
    margin-bottom: 35px;

    &--open {
      display: block;
    }
  }
}
